import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function Main() {
  return (
    <section className="main">
      <div className="main__content-panel">
        <h1>infoplus</h1>
        <div className="content-panel__bullets">
          <BulletTitle title="Simplu" icon="check" i="1" />
          <BulletTitle title="Rapid" icon="bolt" i="2" />
          <BulletTitle title="Intuitiv" icon="lightbulb" i="3" />
        </div>
      </div>

      <Wave />
    </section>
  );
}

function BulletTitle({ title, icon, i }) {
  return (
    <div className="content-panel__bullet">
      <div className="content-panel__icon-bubble">
        <FontAwesomeIcon icon={icon} className={`bullet-icon--${i}`} />
      </div>
      <p>{title}</p>
    </div>
  );
}

function Wave() {
  return (
    <svg
      width="100%"
      height="40%"
      viewBox="0 0 1920 480"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      preserveAspectRatio="none"
      className="main__svg"
    >
      <path
        d="M-2.75467 71.103C-2.75467 71.103 783.533 -88.536 1204.24 71.103C1624.94 230.742 1917.25 57.323 1917.25 57.323V480.003H-2.75467V71.103Z"
        fill="#F1F1F1"
      />
    </svg>
  );
}
